@import '../partials/vars';

/*------------------------------------*\
    LAYOUT
\*------------------------------------*/

.typeahead-container {
    position: relative;
}
.typeahead-query {
    width: 100%;
}

.typeahead-filter {
    position: relative;
}

.typeahead-filter button {
    min-width: 66px;
}

.typeahead-field {
    position: relative;
    display: table;
    border-collapse: separate;
    width: 100%;
}

.typeahead-button {
    position: relative;
    font-size: 0;
    white-space: nowrap;
    width: 1%;
    vertical-align: middle;
}

.typeahead-field > span {
    display: table-cell;
    vertical-align: top;
}


/*------------------------------------*\
    INPUT, BUTTON, A & DROPDOWN
\*------------------------------------*/

.typeahead-button button {
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
}

.typeahead-field input,
.typeahead-select {
    //display: block;
    //width: 100%;
    //height: 32px;
    //padding: 6px 12px;
    //font-size: 13px;
    //line-height: 1.42857143;
    //color: #555555;
    //background: transparent;
    //border: 1px solid #cccccc;
    //border-radius: 2px 0 0 2px;
    //-webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    //box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    //-webkit-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    //-o-transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    //transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    box-sizing: border-box !important;
}

.typeahead-container .typeahead-field input {
    background: #fff;
}

.typeahead-container.hint .typeahead-field input {
    background: transparent;
}

.typeahead-container.hint .typeahead-field input:last-child,
.typeahead-hint {
    background: #fff;
}

.typeahead-container button {
    display: inline-block;
    margin-bottom: 0;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    background-color: #fff;
    border: 1px solid @color-border-light;
    white-space: nowrap;
    padding: 0 .75rem;
    font-size: 1rem;
    height: 38px;
    line-height: 36px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    color: @color-grey-blue-lighter-txt;
    border-radius: 0 .25rem .25rem 0;
}

.typeahead-container button:focus,
.typeahead-container button:active {

}

.typeahead-container button:hover,
.typeahead-container button:focus {
    //color: @color-blue;
    background-color: #f6f8fa;
    //border-color: #adadad;
}

.typeahead-container button:active,
.typeahead-container button.active {
    //outline: 0;
    //background-image: none;
    //-webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    //box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
}

.typeahead-field input.disabled,
.typeahead-field input[disabled],
.typeahead-container button.disabled,
.typeahead-container button[disabled] {
    cursor: not-allowed;
    pointer-events: none;
    opacity: 0.65;
    filter: alpha(opacity=65);
    -webkit-box-shadow: none;
    box-shadow: none;
    background-color: #ffffff;
    border-color: #cccccc;
}

.typeahead-filter button,
.typeahead-button button {
    margin-left: -1px;
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
}

.typeahead-query {
    position: relative;
    z-index: 2;
}

.typeahead-filter,
.typeahead-button {
    z-index: 1;
}

.typeahead-container.filter .typeahead-filter,
.typeahead-filter:hover,
.typeahead-filter:hover,
.typeahead-filter:active,
.typeahead-filter:focus,
.typeahead-button:hover,
.typeahead-button:active,
.typeahead-button:focus,
.typeahead-button:focus button:focus,
.typeahead-button:active button:active {
    z-index: 1001;
}

.typeahead-list,
.typeahead-dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    width: 100%;
    min-width: 160px;
    padding: 0;
    margin: 6px 0 0;
    list-style: none;
    font-size: 1rem;
    text-align: left;
    background-color: #fff;
    border: 1px solid @color-border-light;
    border-radius: .25rem;
    background-clip: padding-box;
}

.typeahead-result.detached .typeahead-list {
    position: relative;
    z-index: 1041;
    top: auto;
    left: auto;
}

.typeahead-dropdown {
    right: 0;
    left: auto;
    z-index: 1001;
}

.typeahead-list > li:first-child {
    border-top: none;
}

.typeahead-list > li {
    position: relative;
    //border-top: solid 1px rgba(0, 0, 0, 0.15);
}

.typeahead-list > li > a,
.typeahead-dropdown > li > a {
    display: block;
    padding: 5px 1rem;
    clear: both;
    font-weight: normal;
    line-height: 1.42857143;
    color: @color-text;
    text-decoration: none;
}
.typeahead-dropdown > li > a {
    white-space: nowrap;
}

.typeahead-list > li > a:hover,
.typeahead-list > li > a:focus,
.typeahead-list > li.active > a,
.typeahead-dropdown > li > a:hover,
.typeahead-dropdown > li > a:focus,
.typeahead-dropdown > li.active > a {
    //background-color: #ebebeb;
    color: @color-blue;
}

.typeahead-list.empty > li > a:hover,
.typeahead-list.empty > li > a:focus,
.typeahead-list.empty > li.active > a {
    background-color: transparent;
}

.typeahead-list.empty > li > a {
    cursor: default;
}

.typeahead-list > li.typeahead-group > a,
.typeahead-list > li.typeahead-group > a:hover,
.typeahead-list > li.typeahead-group > a:focus,
.typeahead-list > li.typeahead-group.active > a {
    border-color: #9cb4c5;
    color: #305d8c;
    background-color: #d6dde7;
    cursor: default;
}

.typeahead-container.result .typeahead-list,
.typeahead-container.filter .typeahead-dropdown,
.typeahead-container.hint .typeahead-hint,
.typeahead-container.backdrop + .typeahead-backdrop {
    display: block !important;
}

.typeahead-container .typeahead-list,
.typeahead-container .typeahead-dropdown,
.typeahead-container .typeahead-hint,
.typeahead-container + .typeahead-backdrop {
    display: none !important;
}


/*------------------------------------*\
    EXTRA
\*------------------------------------*/

.typeahead-dropdown .divider {
    height: 1px;
    margin: 5px 0;
    overflow: hidden;
    background-color: #e5e5e5;
}

.typeahead-caret {
    display: inline-block;
    width: 0;
    height: 0;
    margin-left: 2px;
    vertical-align: middle;
    border-top: 4px solid;
    border-right: 4px solid transparent;
    border-left: 4px solid transparent;
}

.typeahead-search-icon {
    min-width: 40px;
    height: 18px;
    font-size: 13px;
    display: block;
    background:url(data:image/png;base64,) no-repeat scroll center center transparent;
}
.typeahead-display strong { font-weight: 700; }